import {
    Paper,
    FormControl,
    FormControlLabel,
    RadioGroup,
    Radio,
    Checkbox,
    MenuItem,
    InputLabel,
    Select,
    OutlinedInput,
    IconButton,
    InputAdornment,
    CircularProgress,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Typography,
    Box,
    Button,
    TextField,
    Container,
    Grid,
    Avatar,
  } from "@mui/material";
  import CssBaseline from "@mui/material/CssBaseline";
  import React, { useEffect, useState } from "react";
  import { useNavigate } from "react-router-dom";
  import { useSelector, useDispatch } from "react-redux";
  import countryCodes from "country-codes-list";
  import VisibilityOff from "@mui/icons-material/VisibilityOff";
  import Visibility from "@mui/icons-material/Visibility";
//   import homelogo from "../assets/login/homeLogo.png";
  import { SignupActions } from "../store/Signup";
//   import Header from "../components/Header";
  import { toast, ToastContainer } from "react-toastify";
  import { toastOptions } from "../utils/ToastOptions";
  import axios from "axios";
  import UAParser from "ua-parser-js";
  import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
  import ImageIcon from "@mui/icons-material/Image";
  import DeleteIcon from "@mui/icons-material/Delete";
  import CloseIcon from "@mui/icons-material/Close";
  
  import { ref, uploadBytesResumable, getDownloadURL } from "firebase/storage";
  import { storage } from "../firebase/index";
  
  const Signup = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const [showPassword, setShowPassword] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const {
      type,
      email,
      firstName,
      lastName,
      countryNumberCode,
      phone,
      password,
      confirmPassword,
      gender,
      profilePicture,
      fcmToken,
      deviceLanguageCode,
      deviceCountryCode,
      deviceName,
      deviceIdentifier,
    } = useSelector((state) => state.signup);
    const [mobileOptVerify, setMobileOtpVerify] = useState(false);
    const [enteredMobileOtp, setEnteredMobileOtp] = useState("");
    const [enteredEmailOtp, setEnteredEmailOtp] = useState("");

    const [selectedFileURL, setSelectedFileUrl] = useState(null);
  
    const handleClickShowPassword = () => setShowPassword((show) => !show);
  
    const handleMouseDownPassword = (event) => {
      event.preventDefault();
    };
  
    const handleVaidation = () => {
      const emailRegex = /^\S+@\S+\.\S+$/;
      const mobileRegex = /^\d{10}$/;
  
      if (firstName.length <= 0) {
        toast.error("Please enter first name", toastOptions);
        return false;
      }
      if (lastName.length <= 0) {
        toast.error("Please enter last name", toastOptions);
  
        return false;
      }
      if (!gender) {
        toast.error("Please select gender", toastOptions);
        return false;
      }
  
      if (!email) {
        toast.error("Please enter email address", toastOptions);
        return false;
      }
      if (!emailRegex.test(email)) {
        toast.error("Please enter valid email address", toastOptions);
        return false;
      }
  
      if (!password || !confirmPassword) {
        toast.error("Please enter password and confirm password", toastOptions);
        return false;
      }
  
      if (password !== confirmPassword) {
        toast.error("Password and Confirm Password must be same", toastOptions);
        return false;
      }
      if (!countryNumberCode) {
        toast.error("Please select a country code", toastOptions);
        return false;
      }
      if (!phone) {
        toast.error("Please enter phone number", toastOptions);
        return false;
      }
      if (!mobileRegex.test(phone)) {
        toast.error("Please enter Valid phone number", toastOptions);
        return false;
      }
  
      return true;
    };
  
    const sendOtpToNumberAndEmail = async () => {
      try {
        console.log("email,number",  email);
        if (handleVaidation()) {
            console.log("come");
          const emailOtp = await axios.post(
            "https://vast-cyan-peacock-toga.cyclic.app/user/sendotp",
            { perpous:"signup",
            email 
          }
          );
          console.log(emailOtp, "emailOtp");
          setMobileOtpVerify(true);
        }
      } catch (err) {
        toast.error("Otp sending failed", toastOptions);
      }
    };
  
    const handleSignupUser = async (event) => {
      event.preventDefault();
      setMobileOtpVerify(false);
      try {
        const obj = {
          type,
          phone: countryNumberCode + phone,
          email,
          password,
          firstName,
          lastName,
          gender,
          profilePicture,
          device: {
            fcmToken,
            deviceName,
            deviceCountryCode,
            deviceLanguageCode: "fr",
            deviceIdentifier: null,
          },
          emailCode: enteredEmailOtp,
        };
        console.log(obj, "obj");
  
        const { data } = await axios.post(
          "https://vast-cyan-peacock-toga.cyclic.app/user/login",
          obj
        );
        console.log(data, "data----------");
        if (data) {
          toast.success("User registered successfully", toastOptions);
          setShowPassword(false);
          dispatch(SignupActions.clear());
          navigate("/login");
        }
      } catch (err) {
        if (err.response.status === 409) {
          toast.error("User already exists please login", toastOptions);
        }
        if (err.response.status === 403) {
          toast.error("Please enter valid otp", toastOptions);
        }
        console.log(err);
      }
    };
  
    useEffect(() => {}, [confirmPassword, password]);
  
    const countyName = countryCodes.customList(
      "countryCode",
      " {countryNameEn}: +{countryCallingCode}"
    );
  
    const handleImage = async (e) => {
      console.log("9090");
  
      const files = e.target.files;
      const file = files[0];
      // setFileName(file.name);
  
      console.log("fileimage", file);
  
      try {
        const storageRef = ref(storage, `images/user/${file.name}`);
        const uploadTask = uploadBytesResumable(storageRef, file);
  
        uploadTask
          .then(() => {
            getDownloadURL(storageRef)
              .then((url) => {
                dispatch(SignupActions.profilePicture(url));
                setSelectedFileUrl(url);
              })
              .catch((err) => {
                console.log(err);
              });
          })
          .catch((err) => {
            console.log("Upload error", err);
          });
      } catch (error) {
        console.log("Error occurred", error);
      }
    };
  
    useEffect(() => {
      // Retrieve device name
      const parser = new UAParser();
      const userAgent = parser.getResult();
      dispatch(SignupActions.deviceName(userAgent.device.model))
  
      // Retrieve country code
      const fetchCountryCode = async () => {
        try {
          const response = await fetch("https://ipapi.co/json/");
          const data = await response.json();

          dispatch(SignupActions.deviceCountryCode(data.country_code))
        } catch (error) {
          console.log("Error retrieving country code:", error);
        }
      };
  
      fetchCountryCode();
    }, []);
  
    const inputContainerStyle = {
      display: "flex",
      alignItems: "center",
      marginBottom: "10px",
      width: "600px",
    };
  
    const labelStyle = {
      marginRight: "10px",
      fontWeight: "bold",
      width: "20%",
    };
  
    return (
      <>
        {/* <Header /> */}
        <Container component="main" maxWidth="sm">
          <Paper>
            <CssBaseline />
            <Box
              sx={{
                marginTop: 4,
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
              }}
            >
              <img
                src={"homelogo"}
                style={{
                  width: "116px",
                  height: "104px",
                  marginTop: "20px",
                  marginBottom: "25px",
                }}
                alt="logo"
              />
              <Typography
                component="h1"
                variant="h4"
                sx={{ fontWeight: 700, color: "#81201F" }}
              >
                Sign up
              </Typography>
              <Box component="form" noValidate sx={{ mt: 3 }}>
                <FormControl
                  sx={{
                    display: "flex",
                    flexDirection: "row",
                    justifyContent: "center",
                    alignItems: "center",
                  }}
                >
                  <RadioGroup
                    row
                    aria-labelledby="demo-row-radio-buttons-group-label"
                    name="row-radio-buttons-group"
                  >
                    <FormControlLabel
                      name="room"
                      value="User"
                      checked={type === "User"}
                      onChange={() => dispatch(SignupActions.type("User"))}
                      control={<Radio />}
                      label="User"
                    />
                    <FormControlLabel
                      name="room"
                      value="seller"
                      checked={type === "seller"}
                      onChange={() => dispatch(SignupActions.type("seller"))}
                      control={<Radio />}
                      label="Seller"
                    />
                  </RadioGroup>
                </FormControl>
                <Grid container spacing={2}>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      autoComplete="given-name"
                      name="firstName"
                      value={firstName}
                      onChange={(e) =>
                        dispatch(SignupActions.firstName(e.target.value))
                      }
                      required
                      fullWidth
                      id="firstName"
                      label="First Name"
                      autoFocus
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      required
                      fullWidth
                      id="lastName"
                      label="Last Name"
                      name="lastName"
                      value={lastName}
                      onChange={(e) =>
                        dispatch(SignupActions.lastName(e.target.value))
                      }
                      autoComplete="family-name"
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <FormControl fullWidth>
                      <InputLabel id="demo-simple-select-label">
                        Gender
                      </InputLabel>
                      <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        label="Gender"
                        name="gender"
                        value={gender}
                        onChange={(e) =>
                          dispatch(SignupActions.gender(e.target.value))
                        }
                        // onChange={handleChange}
                      >
                        <MenuItem value={"Male"}>Male</MenuItem>
                        <MenuItem value={"Female"}>Female</MenuItem>
                        <MenuItem value={"Other"}>Other</MenuItem>
                      </Select>
                    </FormControl>
                  </Grid>
  
                  <Grid
                    item
                    xs={12}
                    sx={{ display: "flex", flexDirection: "row" }}
                  >
                    <TextField
                      required
                      fullWidth
                      type={"email"}
                      id="email"
                      label="Email Address"
                      name="email"
                      value={email}
                      onChange={(e) =>
                        dispatch(SignupActions.email(e.target.value))
                      }
                      autoComplete="email"
                    />
                  </Grid>
  
                  <Grid item xs={12}>
                    <FormControl variant="outlined" sx={{ width: "100%" }}>
                      <InputLabel htmlFor="outlined-adornment-password">
                        Password
                      </InputLabel>
                      <OutlinedInput
                        name="password"
                        value={password}
                        onChange={(e) =>
                          dispatch(SignupActions.password(e.target.value))
                        }
                        autoComplete="new-password"
                        id="outlined-adornment-password"
                        type={showPassword ? "text" : "password"}
                        endAdornment={
                          <InputAdornment position="end">
                            <IconButton
                              aria-label="toggle password visibility"
                              onClick={handleClickShowPassword}
                              onMouseDown={handleMouseDownPassword}
                              edge="end"
                            >
                              {showPassword ? <VisibilityOff /> : <Visibility />}
                            </IconButton>
                          </InputAdornment>
                        }
                        label="Password"
                      />
                    </FormControl>
                  </Grid>
                  <Grid item xs={12}>
                    <FormControl variant="outlined" sx={{ width: "100%" }}>
                      <InputLabel htmlFor="outlined-adornment-password">
                        Confirm Password
                      </InputLabel>
                      <OutlinedInput
                        autoComplete="new-password"
                        name="confirmPassword"
                        value={confirmPassword}
                        onChange={(e) =>
                          dispatch(SignupActions.confirmPassword(e.target.value))
                        }
                        id="outlined-adornment-password"
                        type={showPassword ? "text" : "password"}
                        endAdornment={
                          <InputAdornment position="end">
                            <IconButton
                              aria-label="toggle password visibility"
                              onClick={handleClickShowPassword}
                              onMouseDown={handleMouseDownPassword}
                              edge="end"
                            >
                              {showPassword ? <VisibilityOff /> : <Visibility />}
                            </IconButton>
                          </InputAdornment>
                        }
                        label="Password"
                      />
                    </FormControl>
                  </Grid>
                  <Grid
                    item
                    xs={12}
                    sx={{ display: "flex", flexDirection: "row" }}
                  >
                    <FormControl sx={{ width: "60%" }}>
                      <InputLabel id="demo-simple-select-label">
                        Country code
                      </InputLabel>
                      <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        name="numbercode"
                        value={countryNumberCode}
                        onChange={(e) =>
                          dispatch(
                            SignupActions.countryNumberCode(e.target.value)
                          )
                        }
                        sx={{ minWidth: "30%" }}
                      >
                        {Object.entries(countyName).map((val, id) => (
                          <MenuItem value={val[1].split(":")[1].trim()}>
                            {val}
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl>
                    <TextField
                      fullWidth
                      label="Mobile Number"
                      name="number"
                      value={phone}
                      onChange={(e) =>
                        dispatch(SignupActions.phone(e.target.value))
                      }
                      variant="outlined"
                      type="tel"
                    />
                  </Grid>
                  <Grid item xs={12}>
                    {profilePicture === null ? (
                      <Box
                        sx={{
                          display: "flex",
                          flexDirection: "row",
                          cursor: "pointer",
                          ml: 2,
                          justifyContent: "center",
                          alignItems: "center",
                        }}
                        onClick={() => {
                          document.getElementById("image-input").click();
                        }}
                      >
                        <ImageIcon />
                        <Typography variant="subtitle1" sx={{ m: 1 }}>
                          Image
                        </Typography>
                        <input
                          id="image-input"
                          hidden
                          accept="image/*"
                          type="file"
                          onChange={handleImage}
                        />
                      </Box>
                    ) : (
                      <Grid
                        container
                        width={"100%"}
                        justifyContent={"space-between"}
                        p={"10px"}
                        sx={{
                          bgcolor: "#d8d8d8",
                          borderRadius: "15px",
                          // position: "absolute",
                          bottom: 0,
                          right: 0,
                        }}
                      >
                        <img src={profilePicture} alt="send file" width="150px" />
                        <Box
                          onClick={() => {
                            dispatch(SignupActions.profilePicture(null));
                            setSelectedFileUrl("");
                          }}
                        >
                          <CloseIcon />
                        </Box>
                      </Grid>
                    )}
                  </Grid>
                </Grid>
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                  }}
                >
                  <Button
                    variant="contained"
                    sx={{
                      backgroundColor: "#B60E0E",
                      width: { md: "180px",xs:"120px" },
                      height: "60px",
                      borderTopRightRadius: "20px",
                      marginTop: "10px",
                      marginBottom: "5px",
                    }}
                    onClick={sendOtpToNumberAndEmail}
                    disabled={isLoading}
                  >
                    {isLoading && (
                      <CircularProgress
                        size={24}
                        sx={{
                          position: "absolute",
                          top: "50%",
                          left: "50%",
                          marginTop: "-12px",
                          marginLeft: "-12px",
                        }}
                      />
                    )}
                    Sign up
                  </Button>
                </Box>
  
                <Dialog
                  open={mobileOptVerify}
                  onClose={() => setMobileOtpVerify(false)}
                >
                  <DialogTitle fontWeight={700}>Please verify otp</DialogTitle>
  
                  <DialogActions>
                    <Grid
                      container
                      direction="column"
                      justifyContent="center"
                      alignItems="center"
                      spacing={2}
                    >
                     
                      <Grid item xs={12} sm={6}>
                        
                          <TextField
                            required
                            fullWidth
                            id="emailotp"
                            label="Email OTP"
                            name="emailotp"
                            type="number"
                            onChange={(e) => setEnteredEmailOtp(e.target.value)}
                            value={enteredEmailOtp}
                            min={1}
                            max={6}
                            // sx={{ width: "70%" }}
                          />
                       
                      </Grid>
                     
                      <Grid item>
                        <Button
                          onClick={handleSignupUser}
                          color="secondary"
                          variant="outlined"
                        >
                          Confirm
                        </Button>
                      </Grid>
                    </Grid>
                  </DialogActions>
                </Dialog>
  
                <Grid container justifyContent="flex-end">
                  <Grid item>
                    <Typography
                      variant="subtitle1"
                      sx={{ cursor: "pointer", mb: 2, mt: 1 }}
                      onClick={() => navigate("/login")}
                    >
                      Already have an account? Sign in
                    </Typography>
                  </Grid>
                </Grid>
              </Box>
            </Box>
          </Paper>
        </Container>
        <ToastContainer />
      </>
    );
  };
  
  export default Signup;
  